.contactPage{
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 80px;
  padding-top: 30px ;
  font-family: sans-serif;
  font-size: 1.2rem;
}

h1{
  margin-bottom: 10px;
  text-shadow: 5px 5px 10px black;
}

.contactInfo{
  padding: 30px 0;
  width: 35vw;
  color: white;
  p{
    line-height : 30px;
  }

  h3{
    margin: 30px 0 10px;
  }

}

.contactForm{
  display: flex;
  align-items: center;
  flex-direction: column;

  width: 35vw;

  color: white;

  padding: 30px 0;

  font-family: sans-serif;

  border: 2px solid white;
  border-radius: 10px;
  box-shadow: 5px 5px 30px black;

  overflow: hidden;
  .textInput{
    height: 30px;
    width: 70%;

    background: transparent;
    color: #ffbe1f;

    margin: 15px;
    padding: 0 10px;

    font-size: 1.2rem;

    border: none;
    border-radius: 10px;
    border-bottom: 1px solid white;

    &::placeholder{

      color: rgba(255, 255, 255, 0.5);
    }
    &:focus{
      border: none;
    }
  }

  .buttonInput{
    width: fit-content;
    font-size: 1.2rem;

    margin-top: 20px;
    border: none;
    border-radius: 5px;
    box-shadow: 5px 5px 10px black;
    padding: 15px;
    cursor: pointer;
    background: #ffbe1f;
    color: black;
    &:hover{
      color: #ffbe1f;
      background: transparent;
      transition: all 500ms;
    }
  }
}


@media (max-width: 769px) {

}
@media (max-width: 425px) {
  .contactPage{
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
  }
  .contactInfo{
    width:90vw;
  }
  .contactForm{
    width: 90vw;
  }
}
