.social{
  position: fixed;
  bottom: 40px;
  left: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.icon{
  margin: 10px;
  color:#FFBE1F;
  }

@media (max-width: 768px) {
  .social{
    position: revert;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 20px 0;
    }
  .icon{
font-size: .8rem;
    }
  }
