*{
  margin: 0;
  padding: 0;
}
div{
    margin: 0;
    padding: 0;
}
h1{
  font-size: 3.8rem;
  color: #ffbe1f;
}

.infoScreen{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .info{
    color: white;
    text-align: justify;
    font-family: "Consolas", sans-serif;
    font-size: x-large;
    position: static;
    margin-top: 35vh;
    width: 65vw;
    padding: 0 30px 20px;
    hr{
      margin: 10px 0;
      border: 1px solid white;
    }
  }

  .nameContainer{
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: left;
  }

  .infoName{
    margin: 0 20px ;
  }

  img{
    border-radius: 50%;
    justify-self: center;
    width: 15%;
    border: 3px solid white;

  }
  .curriculumLink{
    text-align: center;
    width: fit-content;
    margin: 20px auto;
    border-radius: 5px;
    padding: 10px;
    background: #ffbe1f;
    a{
      color: black;
      text-decoration: none;

    }
  }
}

@media (max-width: 768px) {
.infoScreen{

  .info{
    width: 90vw;
  }

  img{
    width: 25%;
  }
}
}
@media (max-width: 425px) {
  h1{
    font-size: 3rem;
  }
  .infoScreen{
    font-size: medium;

    .info{
      width: 80vw;
      position: revert;
      bottom: revert;
      padding: 0;
      margin: 5vh 0 0;
      font-size: medium;
    }

  .nameContainer{
    flex-direction: column;
    text-align: center;
  }
    
    img{
      width: 45vw;
      margin: 20px;
    }
  }
}

