*{
 padding: 0;
  margin: 0;
}

.menu{
  display: flex;
  padding: 0 30px;
  list-style: none;
  background: #051e42;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #0f0c29, #051e42, #0f0c29);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #0f0c29, #051e42, #0f0c29);
}

.containerMenu{
  padding: 10px 0;
  margin: 5px;
}

.menuItem{
  color: #ffbe1f;
  text-decoration: none;
  font-size: large;
  font-family: "BankGothic Lt BT", serif;
  padding: 5px;
  border-radius: 5px;
}

.active{
  color: #000000;
  background: #ffffff;
  text-decoration: none;
  font-family: "BankGothic Lt BT", serif;
}
@media (max-width: 425px) {
  .menu{
    padding: 5px;
    justify-content: center ;
  }
  .containerMenu{
    margin: 1px 2px;
  }
  .menuItem{
    font-size: medium;
  }
}




