h2 {
  color: #ffbe1f;
  font-size: 2.5rem;
}

.projectsPage {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}


.projectContainer {
  display: flex;
  flex-direction: row;

  width: 60vw;

  color: white;

  font-family: Consolas, sans-serif;
  font-size: large;
  text-align: justify;

  padding: 20px ;
  margin: 20px;

  border: 2px solid white;
  border-radius: 20px;

  box-shadow: 5px 5px 15px black;


  img {
    width: 15vw;
    height: 15vw;
    border-radius: 20px;
  }

  .descriptionContainer {
    display: flex;
    flex-direction: column;
    align-items: start;
    margin-left: 30px;


    .projectTitle {
      text-align: center;
      margin-bottom: 15px;
    }

    .description{
    }

    .techContainer {
      justify-content: center;
      display: grid;
      grid-template-columns: repeat(auto-fill, 9rem);
      list-style: none;
      grid-gap: 5px;

      .category {
        width: 100%;
        margin: 0 0 5px 0;
        padding: 5px 0;
        border-radius: 5px;
        color: black;
        background: #ffbe1f;
        text-align: center;
      }

      .tech {
        width: 100%;
        margin: 0 0 5px 0;
        padding: 5px 0;
        border-radius: 5px;
        color: #ffbe1f;
        text-align: center;

      }

    }

  }

}


@media (max-width: 769px) {

  .projectContainer{
    width: 80vw;

    img{
      width: 25vw;
      height: 25vw;
    }

    .descriptionContainer{
      flex-direction: column;
      align-items: start;
      .description{
        margin-left: 0;
      }
      .techContainer{
        grid-template-columns: repeat(auto-fill, 8rem);
        margin-top: 10px;
      }

    }


  }

}
@media (max-width: 425px) {
  .projectContainer{
    width: 80vw;
    flex-direction: column;
    align-items: center;

    img{
      width: 70vw;
      height: 70vw;
      margin: 10px 0;
    }
.descriptionContainer{
  margin: 0;      align-items: center;

  .description{
    margin-left: 0;
    font-size: medium;

    .projectTitle{
      text-align: center;
    }
  }
}

  }
}
