.skillsPage{
  display: grid;
  grid-template-columns: repeat(auto-fit, 20rem);
  margin: 20px 10vw;
  grid-gap: 15px;
  justify-content: center;

  .skill{
    border: 2px solid white;
    padding: 10px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 5px 5px 30px black;
    
    }

}